import React, {useState} from "react";
import styles from "./Paginator.module.css";
import cn from "classnames"


const Paginator = React.memo((props,{portionSize = 10}) => {

    let pagesCount = Math.ceil(props.totalItemsCounts / props.pageSize);
    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }
    let portionCount = Math.ceil(pagesCount / portionSize);
    let [portionNumber, setPortionNumber] = useState(1)
    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
    let rightPortionPageNumber = portionNumber * portionSize;


    return <div className={styles.paginator}>
        {portionNumber > 1 && <button onClick={()=>{setPortionNumber(portionNumber-1)}}>PREF</button>}
        {pages.filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber)
            .map(p => {
                return <span key={p.id} className={cn({
                    [styles.selectPage]:props.currentPage === p},styles.pageNumber)}
                             onClick={() => (props.onPageChanged(p))}>{p}</span>
            })}
        {portionCount > portionNumber && <button onClick={()=>{setPortionNumber(portionNumber+1)}}>NEXT</button>}
    </div>
})

export default Paginator;